import "reset-css";
import "../css/style.sass";

import onePageScroll from "./onepagescroll.js";
import Slides from "./slides.js";

console.log("Before Loaded");

function tmpQueryBg2() {
  const res = {};
  const qs1 = location.search;
  const qs2 = qs1.replace("?", "");
  const qs3 = qs2.split("&");

  for (const val of qs3) {
    const parts = val.split("=");
    const key = parts[0];
    const value = parts[1];
    res[key] = value;
  }
  return res.bg == "2";
}

window.addEventListener("load", () => {
  //tmp
  if (tmpQueryBg2()) {
    document.querySelector("body").classList.add("bg2");
  }
  //tmp

  const mainClassName = ".main";

  const pac = document.querySelector(".pac");
  const underline = document.querySelector(".bottom-bar");

  const paginations = document.querySelectorAll(".pager-item[data-index]");
  const pacFood = document.querySelectorAll(".pagination a");
  const navItems = document.querySelectorAll(".pager-item");

  const slides = document.querySelector(".slide-show");

  const page = new onePageScroll(mainClassName, {
    sectionContainer: "section", // sectionContainer accepts any kind of selector in case you don't want to use section
    easing: "ease", // Easing options accepts the CSS3 easing animation such "ease", "linear", "ease-in",
    // "ease-out", "ease-in-out", or even cubic bezier value such as "cubic-bezier(0.175, 0.885, 0.420, 1.310)"
    animationTime: 800, // AnimationTime let you define how long each section takes to animate
    updateURL: true, // Toggle this true if you want the URL to be updated automatically when the user scroll to each page.
    beforeMove: function(index, _, prev_index) {
      if (isNaN(index)) {
        return;
      }

      if (prev_index < index) {
        pac.classList.remove("reverse");
      } else {
        pac.classList.add("reverse");
      }

      const e = pacFood[index - 1];
      if (e) {
        const t = e.offsetTop;
        let style = t + 1 + "px";
        pac.style.top = style;
      }

      const n = navItems[index - 2];
      if (n) {
        const x = n.offsetLeft;
        const w = n.offsetWidth;
        let style = "translateX(" + x + "px) scaleX(" + w / 100 + ")";
        underline.style.transform = style;
        underline.style.opacity = 1;
      } else {
        underline.style.opacity = 0;
      }

      paginations.forEach(function(elm) {
        if (elm.dataset.index == index) {
          elm.classList.add("active");
        } else {
          elm.classList.remove("active");
        }
      });
    }, // This option accepts a callback function. The function will be called before the page moves.
    loop: false, // You can have the page loop back to the top/bottom when the user navigates at up/down on the first/last page.
    keyboard: true, // You can activate the keyboard controls
    responsiveFallback: false
  });
  global.page = page;

  paginations.forEach(elm => {
    elm.addEventListener("click", event => {
      event.preventDefault();
      event.stopPropagation();
      const idx = elm.dataset.index;
      page.moveTo(idx);
    });
  });

  const body = document.querySelector("body");
  body.classList.add("loading");
  setTimeout(() => {
    document.querySelector(".main").classList.add("loaded");

    body.classList.add("ready");
    body.classList.remove("loading");
  }, 200);

  // const slidesItems = slides.querySelectorAll(".slide");
  // slidesItems.forEach(elem => {
  //   elem.addEventListener("click", () => {
  //     const active = elem.classList.contains("active");
  //     slidesItems.forEach(e => e.classList.remove("active"));
  //     if (!active) {
  //       elem.classList.add("active");
  //     }
  //   });
  // });
  const s = new Slides(slides);
});
