class OnePageScroll {

    constructor(elementSelector, options) {
        const defaults = {
            sectionContainer: "section",
            easing: "ease",
            animationTime: 1000,
            updateURL: false,
            keyboard: true,
            beforeMove: null,
            afterMove: null,
            loop: false,
            responsiveFallback: false
        }
        this.settings = {...defaults, ...options}
        this.el = document.querySelector(elementSelector)
        this.sections = document.querySelectorAll(this.settings.sectionContainer)
        this.total = this.sections.length
        this.topPos = 0
        this.lastAnimation = 0
        this.quietPeriod = Math.floor(this.settings.animationTime * 0.5)
        this.body = document.querySelector("body")
        this.currentIdx = 1

        this.init()
    }

    init() {
        /*-------------------------------------------*/
        /*  Prepare Everything                       */
        /*-------------------------------------------*/

        this._addClass(this.el, "onepage-wrapper")

        for (var i = 0; i < this.sections.length; i++) {
            this._addClass(this.sections[i], "ops-section")
            this.sections[i].dataset.index = i + 1
            this.topPos = this.topPos + 100
        }

        if (window.location.hash != "" && window.location.hash != "#1") {
            const init_index = location.hash.replace("#", "")
            const next = document.querySelector(this.settings.sectionContainer + "[data-index='" + init_index + "']")
                    
            if (next) {
                this.moveTo(parseInt(init_index))
            } else {
                this._addClass(this.sections[this.currentIdx - 1], "active")
                this._addClass(this.body, "viewing-page-" + this.currentIdx)
            }
            
        } else {
            this._addClass(this.sections[this.currentIdx - 1], "active")
            this._addClass(this.body, "viewing-page-" + this.currentIdx)
        }
        
        this._swipeEvents()
        this._responsive()

        if (this.settings.keyboard == true) {
            document.addEventListener('keydown', e => {
                const tag = e
                    .target
                    .tagName
                    .toLowerCase();
    
                if (!this._hasClass(this.body, "disabled-onepage-scroll")) {
                    switch (e.which) {
                        case 38:
                            if (tag != 'input' && tag != 'textarea')
                                this.moveUp()
                            break;
                        case 40:
                            if (tag != 'input' && tag != 'textarea')
                                this.moveDown()
                            break;
                        default:
                            return;
                    }
                }
                return false;
            })
        }

        // const transitionEnd = this._whichTransitionEvent();
        // this.el.addEventListener(transitionEnd, endAnimation, false);
        // const endAnimation = () => {
        //     if (typeof this.settings.afterMove == 'function') {
        //         this.settings.afterMove(index, next_el);
        //     }
        //     this.el.removeEventListener(transitionEnd, endAnimation)
        // }

        return false;
    }

    /*-------------------------------------------------------*/
    /*  Private Functions                                    */
    /*-------------------------------------------------------*/
    /*------------------------------------------------*/
    /*  Credit: Eike Send for the awesome swipe event */
    /*------------------------------------------------*/
    _swipeEvents() {
        let startX = 0
        let startY = 0

        const touchstart = event => {
            console.log("touchstart");
            
            const touches = event.touches;
            if (touches && touches.length) {
                startX = touches[0].pageX;
                startY = touches[0].pageY;
            }
        }

        const touchmove = event => {
            console.log("touchmove");
            

            const timeNow = Date.now()
            // Cancel scroll if currently animating or within quiet period
            if (timeNow - this.lastAnimation < this.quietPeriod) {
                return
            }
            this.lastAnimation = timeNow

            const touches = event.touches;
            if (touches && touches.length) {
                // event.preventDefault();
                const deltaX = startX - touches[0].pageX;
                const deltaY = startY - touches[0].pageY;

                let new_event;
                if (deltaX >= 50) {
                    new_event = new Event('swipeLeft');
                }
                if (deltaX <= -50) {
                    new_event = new Event('swipeRight');
                }
                if (deltaY >= 50) {
                    new_event = new Event('swipeUp');
                }
                if (deltaY <= -50) {
                    new_event = new Event('swipeDown');
                }
                document.dispatchEvent(new_event);

                // if (Math.abs(deltaX) >= 50 || Math.abs(deltaY) >= 50) {
                //     document.removeEventListener('touchmove', touchmove);
                // }
            }
        }

        document.addEventListener("touchmove", touchmove);
        document.addEventListener("touchstart", touchstart);
    }
    /*-----------------------------------------------------------*/
    /*  Utility to add/remove class easily with javascript       */
    /*-----------------------------------------------------------*/

    _hasClass(ele, cls) {
        ele.classList.contains(cls)
    }

    _addClass(ele, cls) {
        ele.classList.add(cls)
    }

    _removeClass(ele, cls) {
        ele.classList.remove(cls)
    }

    /*-----------------------------------------------------------*/
    /*  Transtionend Normalizer by Modernizr                     */
    /*-----------------------------------------------------------*/

    _whichTransitionEvent() {
        const el = document.createElement('fakeelement');
        const transitions = {
            'transition': 'transitionend',
            'OTransition': 'oTransitionEnd',
            'MozTransition': 'transitionend',
            'WebkitTransition': 'webkitTransitionEnd'
        }

        for (const t in transitions) {
            if (el.style[t] !== undefined) {
                return transitions[t];
            }
        }
    }

    /*-----------------------------------------------------------*/
    /*  Function to perform scroll to top animation              */
    /*-----------------------------------------------------------*/

    _scrollTo(element, to, duration) {
        if (duration < 0) {
            return;
        }
        const difference = to - element.scrollTop;
        const perTick = difference / duration * 10;

        setTimeout(function () {
            element.scrollTop = element.scrollTop + perTick;
            if (element.scrollTop == to) {
                return;
            }
            this._scrollTo(element, to, duration - 10);
        }, 10);
    }

    /*---------------------------------*/
    /*  Function to transform the page */
    /*---------------------------------*/

    _transformPage(pos) {
        const transform = "translate3d(0, " + pos + "%, 0);"
        const transition = "transform " + this.settings.animationTime + "ms " + this.settings.easing + ";"
        const transformCSS = "-webkit-transform: " + transform + " -webkit-transition: -webkit-" + transition + " -moz-transform: " + transform + " -moz-transition: -moz-" + transition + " -ms-transform: " + transform + " -ms-transition: -ms-" + transition + " transform: " + transform + " transition: " + transition

        this.el.style.cssText = transformCSS;
    }

    /*-------------------------------------------*/
    /*  Responsive Fallback trigger              */
    /*-------------------------------------------*/

    _responsive() {

        const _mouseWheelHandler = event => {
            event.preventDefault()
            const delta = event.wheelDelta || -event.detail
            if (!this._hasClass(this.body, "disabled-onepage-scroll")) {
                this._init_scroll(delta);
            }
        }

        const swipeDown = event => {
            if (!this._hasClass(this.body, "disabled-onepage-scroll")) {
                event.preventDefault();
            }
            this.moveUp();
        }

        const swipeUp = event => {
            if (!this._hasClass(this.body, "disabled-onepage-scroll")) {
                event.preventDefault();
            }
            this.moveDown();
        }

        const resize = () => {
            if (window.innerWidth < this.settings.responsiveFallback) {
    
                this._addClass(this.body, "disabled-onepage-scroll");
                document.removeEventListener('mousewheel', _mouseWheelHandler);
                document.removeEventListener('DOMMouseScroll', _mouseWheelHandler);
                document.removeEventListener("swipeDown", swipeDown);
                document.removeEventListener("swipeUp", swipeUp);
            } else {
    
                if (this._hasClass(this.body, "disabled-onepage-scroll")) {
                    this._removeClass(this.body, "disabled-onepage-scroll");
                    this._scrollTo(document.documentElement, 0, 2000);
                }
    
                document.addEventListener("swipeDown", swipeDown);
                document.addEventListener("swipeUp", swipeUp);
                document.addEventListener('mousewheel', _mouseWheelHandler);
                document.addEventListener('DOMMouseScroll', _mouseWheelHandler);
            }
        }

        if (this.settings.responsiveFallback != false) {
            window.onresize = resize
            resize()
        } else {
            document.addEventListener("swipeDown", swipeDown);
            document.addEventListener("swipeUp", swipeUp);
            document.addEventListener('mousewheel', _mouseWheelHandler);
            document.addEventListener('DOMMouseScroll', _mouseWheelHandler);
        }
    }

    /*-------------------------------------------*/
    /*  Initialize scroll detection              */
    /*-------------------------------------------*/

    _init_scroll(delta) {
        const deltaOfInterest = delta
        const timeNow = Date.now()

        // Cancel scroll if currently animating or within quiet period
        if (timeNow - this.lastAnimation < this.quietPeriod) {
            return
        }
        // Prevent wrong scroll delta
        if(Math.abs(deltaOfInterest) <= 1) {
            return
        }

        this.lastAnimation = timeNow

        if (deltaOfInterest < 0) {
            this.moveDown()
        } else {
            this.moveUp()
        }

    }

    /*-------------------------------------------------------*/
    /*  Public Functions                                     */
    /*-------------------------------------------------------*/

    /*---------------------------------*/
    /*  Function to move down section  */
    /*---------------------------------*/

    moveDown() {
        let next_index = this.currentIdx + 1
        if(next_index > this.total) {
            if(this.settings.loop) {
                this.moveTo(1)
            }
        } else {
            this.moveTo(next_index)
        }
    }

    /*---------------------------------*/
    /*  Function to move up section    */
    /*---------------------------------*/

    moveUp() {
        let next_index = this.currentIdx - 1
        if(next_index <= 0) {
            if (this.settings.loop) {
                this.moveTo(this.total)
            }
        } else {
            this.moveTo(next_index)
        }
    }

    /*-------------------------------------------*/
    /*  Function to move to specified section    */
    /*-------------------------------------------*/

    moveTo(next_index) {
        next_index = parseInt(next_index)

        const current = this.sections[this.currentIdx - 1]
        const next = this.sections[next_index - 1];
        
        this._removeClass(current, "active");
        this._addClass(next, "active");

        this.body.className = this.body.className.replace(/\bviewing-page-\d.*?\b/g, '');
        this._addClass(this.body, "viewing-page-" + next_index);

        if (history.replaceState && this.settings.updateURL == true) {
            window.location.hash = "#" + next_index
        }
        if (typeof this.settings.beforeMove == 'function') {
            this.settings.beforeMove(next_index, next, this.currentIdx)
        }
        this.currentIdx = next_index

        const pos = ((next_index - 1) * 100) * -1;
        this._transformPage(pos, next_index, next);
    }
}

export default OnePageScroll
