class Slides {
  constructor(elem, options) {
    const defaults = {
      animationTime: 1000
    };
    this.options = { ...defaults, ...options };

    this.root = elem;
    this.childs = Array.prototype.slice
      .call(elem.childNodes)
      .filter(e => e.nodeType == 1);

    console.log("Slice::", this.root, this.childs);

    if (this.childs.length < 2) {
      throw new Error("Too few childs in slides");
    }
    this.active = this.childs[0];
    this.active_height = this.active.clientHeight;
    this.others = this.childs.filter(e => e != this.active);

    this.childs.forEach((elem, idx) => {
      elem.addEventListener("click", event => {
        if (this.updating) return;
        event.preventDefault();
        this.active_height = this.active.clientHeight;
        this.swipe_active(elem);
      });
      elem.dataset.slideid = idx;
      elem.style.transition = this.options.animationTime + "ms";
    });

    this.update();
  }

  swipe_active(elem) {
    if (!this.childs.includes(elem)) return;
    if (this.active == elem) return;

    const other_pos = this.others.indexOf(elem);
    this.others[other_pos] = this.active;
    this.active = elem;
    this.update();
  }

  update() {
    if (this.updating) return;

    this.updating = true;

    this.active.style.width = "100%";
    this.active.style.top = "0";
    this.active.style.left = "0";
    this.active.classList.add("pre-active");
    console.log("Slide::active size", this.active_height);

    const l = this.others.length;
    const other_w = (1 / l) * 100;
    this.others.forEach((e, i) => {
      e.classList.remove("active");
      e.classList.remove("pre-active");
      e.style.width = other_w + "%";
      e.style.top = this.active_height + "px";
      e.style.left = i * other_w + "%";
    });

    setTimeout(() => {
      this.active.classList.add("active");
      this.updating = false;
    }, this.options.animationTime);
  }
}

export default Slides;
